* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}
.main-header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
}

.content {
  margin-top: 3.5rem;
  padding: 1rem;
}

@media (min-width: 768px) {
  .content {
    margin-top: 3.5rem;
    padding: 1rem 2rem;
  }
}

.backdrop {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
  opacity: 1;
}

.toolbar {
    width: 100%;
    height: 3.5rem;
    background: #6E8E88;
}
.mobile-toggle {
  background: transparent;
  border: none;
  display: flex;
  width: 2.5rem;
  height: 80%;
  justify-content: center;
  cursor: pointer;
  padding: 0;
}

.mobile-toggle__bar {
  width: 2.5rem;
  height: 4px;
  background: white;
}

.mobile-toggle__icon{
  width:25px;
  height:25px;
  fill:white;
}

@media (min-width: 768px) {
    .mobile-toggle {
        display: none;
    }
}
.logo {
    font-size: 1rem;
    font-weight: bold;
    padding: 0.5rem;
    color: white;
    display:flex;
    align-items: center;
}
.logo-image{
    width: 32px;
    height: 32px;
    margin-right: 1rem;
}
.navigation-item {
  padding: 0 1.5rem;
}

.navigation-item a {
  text-decoration: none;
  color: white;
}

.navigation-item.mobile {
  font-size: 1.75rem;
  margin: 0.5rem 0;
}

.navigation-item.mobile a {
  color: #EAC6B8;
}

.navigation-item a:hover,
.navigation-item a:active,
.navigation-item a.active {
  color: #D08562;
}

.navigation-item:last-of-type {
  padding-right: 0;
}

.navigation-item button {
  font: inherit;
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0;
}

.navigation-item button:hover,
.navigation-item button:active {
  color: #fab83f;
}

.main-nav {
  height: 100%;
  padding: 0 1rem;
  display: flex;
  align-items: center;
}

.spacer {
  flex: 1 1;
}

.main-nav__home-link{
  text-decoration: none;
}

.main-nav__items {
  list-style: none;
  padding: 0;
  margin: 0 1.5rem;
  display: none;
}

@media (min-width: 768px) {
  .main-nav__items {
    display: flex;
  }
}

.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 30rem;
  max-width: 90%;
  background: white;
  box-shadow: 1px 0 8px rbga(0, 0, 0, 0.26);
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  z-index: 200;
  padding: 3rem 2rem;
}

.mobile-nav.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.mobile-nav__items {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
}

.mobile-nav__items.mobile {
    flex-direction: column;
}

.button {
  font: inherit;
  color: #D08562;
  background: none;
  padding: 0.6rem;
  cursor: pointer;
  text-transform: uppercase;
  text-decoration: none;
  border: none!important;
}

.button:focus {
  outline: none;
}

.button:disabled {
  background: #ccc;
  color: #888888;
  cursor: not-allowed;
  border: #ccc;
  box-shadow: none;
}

.button:disabled:hover,
.button:disabled:active {
  background: #ccc;
  color: #888888;
  border: #ccc;
}

.button--flat {
  border: none;
}

.button--raised {
  background: #D08562;
  color: white;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.26);
}

.button--accent:hover,
.button--accent:active {
  background: #fab83f;
  color: #D08562;
}

.button--flat.button--accent:hover,
.button--flat.button--accent:active {
  background: rgba(250, 184, 63, 0.3);
  color: #fab83f;
}

.button.button--accent.button--raised {
  background: #D08562;
  color: white;
}

.button--raised.button--accent:hover,
.button--raised.button--accent:active {
  background: #fbc766;
}

.button.button--danger {
  border-color: #6E8E88;
  color: #6E8E88;
}

.button.button--success {
  border-color: #00b359;
  color: #00b359;
}

.button--success:hover,
.button--success:active {
  background: #00b359;
  color: white;
}

.button--flat.button--success:hover,
.button--flat.button--success:active {
  background: rgba(0, 179, 90, 0.3);
  color: #00b359;
}

.button.button--success.button--raised {
  background: #00b359;
  color: white;
}

.button--raised.button--success:hover,
.button--raised.button--success:active {
  background: #00a151;
}

.modal {
  position: fixed;
  width: 90%;
  left: 5%;
  background: white;
  border-radius: 5px;
  z-index: 200;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  max-height:700px;
  overflow-y: scroll;
  top:0;
  padding-bottom: 50px;
}

.modal__header {
  border-bottom: 2px solid #D08562;
}

.modal__header h1 {
  font-size: 1.5rem;
  color: #D08562;
  margin: 1rem;
}

.modal__content {
  padding: 1rem;
}

.modal__actions {
  padding: 1rem;
  text-align: right;
}

.modal__actions button {
  margin: 0 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    width: 40rem;
    left: calc((100% - 40rem) / 2);
  }
}

.post {
  width: 25%;
  padding: 0.3rem;
  display: flex;
}

.post__info{
  padding: 1rem;
}

.post__wrapper{
  border: 1px solid #D08562;
  border-radius: 5px;
  width: 100%;
}

.post__meta {
  font-size: 0.8rem;
  color: #707070;
  margin: 0;
  font-weight: lighter;
}

.post__title {
  font-size: 1.5rem;
  margin: 1rem 0;
  color: #D08562;
}

.post__image {
  height: 15rem;
  width: 100%;
}

.post__actions {
  text-align: left;
}

.post__warning{
  font-style: italic;
  font-size: small;
}

@media (max-width: 768px) {
  .post {
    width: 50%;
  }
}

@media (max-width: 768px) {
  .post {
    width: 50%;
  }
}

@media (max-width: 525px) {
  .post {
    width: 100%;
  }
}

.input {
  margin: 1rem 0;
  width: 100%;
}

.input label {
  display: block;
  margin-bottom: 0.25rem;
}

.input input,
.input textarea {
  display: block;
  font: inherit;
  padding: 0.25rem 0.5rem;
  width: 100%;
  border-radius: 3px;
  border: 1px solid #ccc;
}

.input .touched.invalid {
    border-color: red;
    background: #ffc2c2;
}

.input input:focus,
.input textarea:focus {
  outline: none;
  border-color: #D08562;
  color: #D08562;
}

.image {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
}
.mapBoxMap{
    height: 250px;
}

.mapBoxMap button{
    border: none;
    background: none;
}

.mapBoxMap__icon{
    height: 25px;
    width: 25px;
} 
.paginator__controls {
  display: flex;
  justify-content: center;
}

.paginator__control {
  width: 5rem;
  padding: 0.25rem 0;
  margin: 0 1rem;
  border: 1px solid #D08562;
  background: transparent;
  font: inherit;
  cursor: pointer;
  font-size: 1rem;
  color: #D08562;
}

.paginator__control:hover,
.paginator__control:active {
  color: #fab83f;
  border-color: #fab83f;
}

.paginator__control:focus {
  outline: none;
}

.loader {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  .loader div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 6px solid #6E8E88;
    border-radius: 50%;
    -webkit-animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #6E8E88 transparent transparent transparent;
  }
  .loader div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .loader div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .loader div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes loader {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.feed__status {
  width: 90%;
  margin: 1rem auto;
}

.feed__status form {
  display: flex;
  align-items: center;
}

.feed__status form * {
  margin: 0 0.5rem;
}

.feed__control {
  text-align: center;
}

.feed__posts{
  display:flex;
  flex-wrap: wrap;
  max-width: 1000px;
  margin: 0 auto;
}

.feed h2, .feed p{
  text-align: center;
  font-weight: lighter;
}

.feed__warning{
  font-style: italic;
  font-size: small;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid gray;
  margin-bottom: 35px;
  border-radius: 3px;
  color: gray;
}

.new-post__preview-image {
  width: 15rem;
  height: 7rem;
}

#feed__hero{
  min-height: 65vh;
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  padding: 15px;
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 80px;
}

#feed__hero h1{
  font-family: 'Mali', cursive;
  font-weight: 200;
  font-size: 3rem;
}

#feed__hero img{
  max-width: 100%;
}

.feed-hero__text{
  display: flex;
  align-items: center;
  flex-direction: column;
  width:30%;
  text-align:center;
}

.feed-hero__img{
  width: 70%;
}

@media (max-width:768px){
  .feed-hero__text, .feed-hero__img{
    width:100%
  }
}

@media (min-width: 768px) {
  .feed__status {
    width: 30rem;
  }
}

.single-post {
  width: 90%;
  margin: auto;
  text-align: center;
}

.single-post h1{
  color: #D08562;
}

.single-post h2 {
  font-size: 1rem;
  color: #464646;
  padding-bottom: 1rem;
  font-weight: lighter;
}

.single-post p{
  color: #464646
}

.single-post__image {
  height: 25rem;
  width: 100%;
  margin: 1rem auto;
}

@media (min-width: 768px) {
  .single-post {
    width: 40rem;
  }
}

.auth__wrapper{
  margin-top: 50px;
  min-height: 90vh;
  display: flex;
}

.auth-form {
    max-width: 400px;
    margin: auto;
    padding: 1rem;
    border: 1px solid #D08562;
    border-radius: 5px;
  }

  .auth-form h1{
    font-family: 'Mali', cursive;
  }

  .login__noAccount, .login__validate, .auth__intro{
    text-align: center;
  }

  .auth__intro img{
    height:60px
  }


  
  
  @media (min-width: 768px) {
    .auth-form {
      width: 40rem;
    }
  }
  

